export const createData = (length) => {
  return new Promise((resolve, reject) => {
    let data = []
    for (let i = 0; i < length; i++) {
      data.push({
        col0: i,
        col1: '',
        col2: '',
        col3: 'xx',
        col4: '',
        col5: '',
        col6: 'xxxxxxxxxxx',
        col7: 'xxxx-xx-xx'
      })
    }
    resolve(data)
  })
}



