<!-- - -->
<template>
  <div class="i-container" :style="style">
    <i-tabbar ref="tabbar">
      <i-tabbar-item link="/classroom/detail" name=""></i-tabbar-item>
      <i-tabbar-item link="/classroom/detail/info" name=""></i-tabbar-item>
      <i-tabbar-item link="/classroom/detail/record" name=""></i-tabbar-item>
      <i-tabbar-item link="/classroom/detail/doc" name=""></i-tabbar-item>
    </i-tabbar>
    <i-menubar title="" sup="" :has-tabbar="hasTabbar" :show-filter="false">
      <span slot="ctrl">
        <el-button size="small" class="i-button__ctrl i-button__primary"></el-button>
        <el-button size="small" class="i-button__ctrl i-button__danger"></el-button>
      </span>
    </i-menubar>
    <router-view></router-view>
  </div>
</template>

<script>
  import ITabbar from '@/components/tabbar/tabbar'
  import ITabbarItem from '@/components/tabbar/tabbar-item'
  import IMenubar from '@/components/menubar'
  import ISection from '@/components/section'
  import { mapGetters } from 'vuex'
  import { scrollMixins } from '@/mixins/scroll'
  import src from '@/assets/img/ico.jpg'

  export default {
    name: 'StudentDetail',
    components: {
      ITabbar,
      ITabbarItem,
      IMenubar,
      ISection
    },
    mixins: [scrollMixins],
    computed: {
      style () {
        let color = '#f5f5f7'
        document.body.style.background = `${color}`
        return ({ background: `${color}` })
      },
      ...mapGetters([
        'card'
      ])
    },
    methods: {
      showPopover () {
        this.text = ''
      },
      hidePopover () {
        this.text = ''
      }
    },
    data () {
      return {
        text: '',
        src: src
      }
    }
  }
</script>

<style lang="scss" scoped>
  .i-carte {
    padding: 30px 40px;
    text-align: center;
    > img {
      display: inline-block;
      width: 80px;
      height: 80px;
      border-radius: 100%;
    }
    > h1 {
      font-size: 26px;
      line-height: 26px;
      margin-top: 20px;
      color: #0d2039;
    }
    > p {
      font-size: 20px;
      line-height: 20px;
      margin-top: 10px;
      margin-bottom: 30px;
      color: rgba(54, 72, 97, 0.9);
    }
  }
</style>



