import Vue from 'vue'
import Router from 'vue-router'
import LoadingBar from '@/components/loading-bar'
Vue.use(LoadingBar)

// 
import Home from '@/views/home/Index' // 
import Schedule from '@/views/home/Schedule' // 
import HomeDone from '@/views/home/Done' // 
import HomeData from '@/views/home/Data' // 
// 
import Setting from '@/views/setting/Index' // 
import System from '@/views/setting/System' // 
import Information from '@/views/setting/Info' // 
import Admin from '@/views/setting/Admin' // 
// 
import Student from '@/views/student/Index' // 
import StudentDetail from '@/views/student/detail/Index' // -
import StudentDetailPlan from '@/views/student/detail/Plan' // 
import StudentDetailInfo from '@/views/student/detail/Info' // 
import StudentDetailRecord from '@/views/student/detail/Record' // 
import StudentDetailDoc from '@/views/student/detail/Doc' // 
import StudentAdd from '@/views/student/Add' // 
// 
import Teacher from '@/views/teacher/Index' // 
import TeacherDetail from '@/views/teacher/detail/Index' // -
import TeacherDetailPlan from '@/views/teacher/detail/Plan' // 
import TeacherDetailInfo from '@/views/teacher/detail/Info' // 
import TeacherDetailRecord from '@/views/teacher/detail/Record' // 
import TeacherAdd from '@/views/teacher/Add' // 
// 
import Classroom from '@/views/classroom/Index' // 
import ClassroomDetail from '@/views/classroom/detail/Index' // -
import ClassroomDetailPlan from '@/views/classroom/detail/Plan' // 
import ClassroomDetailInfo from '@/views/classroom/detail/Info' // 
import ClassroomDetailRecord from '@/views/classroom/detail/Record' // 
import ClassroomDetailDoc from '@/views/classroom/detail/Doc' // 
import ClassroomAdd from '@/views/classroom/Add' // 

Vue.use(Router)

const route = new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          path: '',
          component: Schedule,
          name: 'Schedule'
        },
        {
          path: 'home/done',
          component: HomeDone,
          name: 'HomeDone'
        },
        {
          path: 'home/data',
          component: HomeData,
          name: 'HomeData'
        }
      ]
    },
    {
      path: '/setting',
      component: Setting,
      children: [
        {
          path: '',
          component: Information,
          name: 'Setting'
        },
        {
          path: 'system',
          component: System,
          name: 'System'
        },
        {
          path: 'admin',
          component: Admin,
          name: 'Admin'
        }
      ]
    },
    {
      path: '/student',
      component: Student,
      name: 'Student'
    },
    {
      path: '/student/detail',
      component: StudentDetail,
      children: [
        {
          path: '',
          name: 'StudentDetailPlan',
          component: StudentDetailPlan
        },
        {
          path: 'info',
          name: 'StudentDetailInfo',
          component: StudentDetailInfo
        },
        {
          path: 'record',
          name: 'StudentDetailRecord',
          component: StudentDetailRecord
        },
        {
          path: 'doc',
          name: 'StudentDetailDoc',
          component: StudentDetailDoc
        }
      ]
    },
    {
      path: '/student/add',
      name: 'StudentAdd',
      component: StudentAdd
    },
    {
      path: '/teacher',
      component: Teacher,
      name: 'Teacher'
    },
    {
      path: '/teacher/detail',
      component: TeacherDetail,
      children: [
        {
          path: '',
          name: 'TeacherDetailPlan',
          component: TeacherDetailPlan
        },
        {
          path: 'info',
          name: 'TeacherDetailInfo',
          component: TeacherDetailInfo
        },
        {
          path: 'record',
          name: 'TeacherDetailRecord',
          component: TeacherDetailRecord
        }
      ]
    },
    {
      path: '/teacher/add',
      name: 'TeacherAdd',
      component: TeacherAdd
    },
    {
      path: '/classroom',
      component: Classroom,
      name: 'Classroom'
    },
    {
      path: '/classroom/detail',
      component: ClassroomDetail,
      children: [
        {
          path: '',
          name: 'ClassroomDetailPlan',
          component: ClassroomDetailPlan
        },
        {
          path: 'info',
          name: 'ClassroomDetailInfo',
          component: ClassroomDetailInfo
        },
        {
          path: 'record',
          name: 'ClassroomDetailRecord',
          component: ClassroomDetailRecord
        },
        {
          path: 'doc',
          name: 'ClassroomDetailDoc',
          component: ClassroomDetailDoc
        }
      ]
    },
    {
      path: '/classroom/add',
      name: 'ClassroomAdd',
      component: ClassroomAdd
    }

  ]
})

route.beforeEach((to, from, next) => {
  LoadingBar.start()
  next()
})
route.afterEach(route => {
  LoadingBar.finish()
})

export default route



